import axios from 'axios';
import React, { useState, useEffect } from 'react'
import { useNavigate, useParams } from 'react-router-dom'
import { update_data } from '../actions/inputData'
import { ToastContainer } from 'react-toastify'
import { connect, useDispatch } from 'react-redux'

function UpdateForm({
    update_data
}) {

    const [selectedDivision, setSelectedDivision] = useState('Choose...'); //default value
    const [selectedEstate, setSelectedEstate] = useState('Choose...'); //default value
    const [selectedWeight, setSelectedWeight] = useState('')
    const [selectedDate, setSelectedDate] = useState('')
    const [selectedId, setSelectedId] = useState('')

    const [divs, setDivs] = useState([])
    const [estates, setEstate] = useState([])

    const dispatch = useDispatch()

    useEffect(() => {
        axios.get(`${process.env.REACT_APP_API_URL}/input/div_choice`)
            .then((response) => {
                setDivs(response.data)
                console.log(response.data)
            })
            .catch(error => console.error(`Error: ${error}`))
    }, [])

    useEffect(() => {
        axios.get(`${process.env.REACT_APP_API_URL}/input/ret_estate`)
            .then((response) => {
                setEstate(response.data)
                console.log(response.data)
            })
            .catch(error => console.error(`Error: ${error}`))
    }, [])

    const navigate = useNavigate()
    const { id } = useParams()

    const loadData = async () => {
        const res = await axios.get(`${process.env.REACT_APP_API_URL}/input/input_data_ret/${id}`)
        console.log(res.data)
        setSelectedId(res.data.id)
        setSelectedEstate(res.data.estate)
        setSelectedDivision(res.data.division)
        setSelectedWeight(res.data.weight)
        setSelectedDate(res.data.date)
    }

    useEffect(() => {
        loadData()
    }, [])

    function handleSelectEstateChange(event) {
        setSelectedEstate(event.target.value)
    }

    function handleSelectDivisionChange(event) {
        setSelectedDivision(event.target.value);
    }

    function handleSelectWeightChange(event) {
        setSelectedWeight(event.target.value)
    }

    function handleSelectDateChange(event) {
        setSelectedDate(event.target.value)
    }

    const onSubmit = e => {
        e.preventDefault();
        update_data(selectedId, selectedEstate, selectedDivision, selectedWeight, selectedDate)
    }

    return (
        <div className="container">
            <form onSubmit={e => onSubmit(e)}>
                <div className="w-75 mx-auto shadow p-5">
                    <h2 className="text-center mb-4">Add</h2>

                    <div className="form-group">
                        <label className='form-label mt-3' htmlFor='estate'>Estate</label>
                        <select className="form-control" value={selectedEstate} onChange={handleSelectEstateChange}>
                            <option value="">Choose...</option>
                            {
                                estates.map((estate) => {
                                    return (
                                        <option value={estate.key}>{estate.value}</option>
                                    )
                                })
                            }
                        </select>
                    </div>

                    <div className="form-group">
                        <label className='form-label mt-3' htmlFor='division'>Division</label>
                        <select className="form-control" value={selectedDivision} onChange={handleSelectDivisionChange}>
                            <option value="">Choose...</option>
                            {
                                divs.map((div) => {
                                    return (
                                        <option value={div.key}>{div.value}</option>
                                    )
                                })
                            }
                        </select>
                    </div>

                    <div className="form-group">
                        <label className='form-label mt-3' htmlFor='weight'>Weight</label>
                        <input
                            type="number"
                            className="form-control"
                            placeholder="Enter Weight"
                            name="weight"
                            value={selectedWeight}
                            onChange={handleSelectWeightChange}
                        />
                    </div>
                    <div className="form-group">
                        <label className='form-label mt-3' htmlFor='date'>Date</label>
                        <input className="form-control" type="date" name="date" placeholder='Choose Date' value={selectedDate} onChange={handleSelectDateChange} />
                    </div>
                    <button className="btn btn-primary btn-block mt-3" type='submit'>Update Data</button>

                </div>
            </form>
            <ToastContainer
                position="top-center"
                autoClose={5000}
                hideProgressBar={false}
                newestOnTop={false}
                closeOnClick
                rtl={false}
                pauseOnFocusLoss
                draggable
                pauseOnHover
            />
        </div>
    )
}

const mapStateToProps = (state, props) => ({
    first_name_global: state.profile.first_name,
    last_name_global: state.profile.last_name,
    company_global: state.profile.company
  })

export default connect(mapStateToProps, { update_data, })(UpdateForm)