import React, { useState, useEffect } from 'react'
import { Line } from 'react-chartjs-2'
import axios from 'axios'
import { Chart, registerables } from 'chart.js';
Chart.register(...registerables);

function Dashboard() {

    const [getDatas, setGetDatas] = useState([])
    const [filterEstate, setFilterEstate] = useState('Tiara 1')
    const [estates, setEstates] = useState([])
    const [latestMonth, setLatestMonth] = useState()
    const [latestYear, setLatestYear] = useState()

    const months = [
        { month: "Jan", value: 0 },
        { month: "Feb", value: 1 },
        { month: "Mar", value: 2 },
        { month: "Apr", value: 3 },
        { month: "May", value: 4 },
        { month: "Jun", value: 5 },
        { month: "Jul", value: 6 },
        { month: "Aug", value: 7 },
        { month: "Sep", value: 8 },
        { month: "Oct", value: 9 },
        { month: "Nov", value: 10 },
        { month: "Dec", value: 11 },
    ];

    useEffect(() => {
        axios.get(`${process.env.REACT_APP_API_URL}/input/input_data_ret`)
            .then((response) => {
                setGetDatas(response.data)
            })
            .catch(error => console.error(`Error: ${error}`))
    }, [])

    useEffect(() => {
        axios.get(`${process.env.REACT_APP_API_URL}/input/ret_estate`)
            .then((response) => {
                setEstates(response.data)
            })
            .catch(error => console.error(`Error: ${error}`))
    }, [])

    function handleFilterEstate(event) {
        setFilterEstate(event.target.value)
    }

    function testDate1(x) {
        const getDate = new Date(x.date);
        const getMonth = getDate.getMonth()
        const getYear = getDate.getFullYear()
        return {getMonth, getYear}
    }

    function testDate2() {
        const latestDate = new Date(Math.max(...getDatas.map(e => new Date(e.date))));
        const getMonth = latestDate.getMonth()
        const getYear = latestDate.getFullYear()
        return{getMonth, getYear}
    }

    const currentMonthData = getDatas.filter((getData) => {
        if (testDate1(getData.date) === testDate2() ) {
            return getData
        }
    })

    const dateConverted = getDatas.map((x) => {
        const test = new Date(Math.max(new Date(x.date)))
        // const d = test.getMonth() + 1
        // const b = getDatas.filter((y) => {
        //     if (x.value === d) {
        //         return x
        //     }
        // })
        return test
    })

    const state1 = {
        labels: getDatas.filter(data => data.estate.includes(filterEstate)).map(x => x.date),
        datasets: [
            {
                label: 'Actual',
                backgroundColor: 'rgba(75,192,192)',
                lineTension: 0.1,
                borderColor: 'rgba(75,192,192)',
                data: getDatas.filter(data => data.estate.includes(filterEstate)).map(x => x.weight),
            },
            {
                label: 'Estimate',
                backgroundColor: 'rgba(89,11,192)',
                lineTension: 0.1,
                borderColor: 'rgba(89,11,192)',
                data: [65, 59, 80, 81, 56],
            }
        ]
    }

    return (
        <div className='container mt-5'>
            <h1 className='mt-3 mb-3'>Dashboard</h1>
            <div className="container chart">
                <form>
                    <div className='form-group d-flex '>
                        <h3>RSB POM ({filterEstate})</h3>
                        <div className='d-flex'>
                            <label htmlFor="estate-filter">Estate:</label>
                            <select className="form-control" value={filterEstate} onChange={handleFilterEstate}>
                                {
                                    estates.map((x) => {
                                        return (
                                            <option value={x.key}>{x.value}</option>
                                        )
                                    })
                                }
                            </select>
                        </div>
                    </div>
                </form>
                <h4>Overall Progress (Current Month)</h4>
                <Line
                    data={state1}
                    options={{
                        responsive: true,
                        plugins: {
                            legend: {
                                position: 'right'
                            }
                        },
                        title: {
                            display: true,
                            text: 'Weight for the month'
                        },
                    }}
                />
            </div>
        </div>
    )
}

export default Dashboard