import React from 'react';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import Layout from './hocs/Layout'
import Home from './containers/Home'
import Register from './containers/Register'
import Login from './containers/Login'
import Profile from './containers/Profile'
import PrivateRoute from './hocs/PrivateRoute'
import InputData from './containers/InputData';
import AddInputData from './containers/InputDataForm';
import UpdateForm from './containers/UpdateForm';
import Dashboard from './containers/Dashboard';

import { Provider } from 'react-redux'
import store from './store'
import "./static/navbar.css"


const App = () => (
    <Provider store={store}>
        <Router>
            <Layout />
            <Routes>
                {/* <Route element={<Home />} path="/" /> */}
                <Route path="/register" element={<Register />} />
                <Route path="/" element={<Login />} />
                {/* <Route path="/dashboard" element={<Dashboard />} /> */}
                <Route path="/profile" element={
                    <PrivateRoute>
                        <Profile/>
                    </PrivateRoute>
                } />
                <Route path="/input_data" element={
                    <PrivateRoute>
                        <InputData/>
                    </PrivateRoute>
                } />
                <Route path="/input_form" element={
                    <PrivateRoute>
                        <AddInputData/>
                    </PrivateRoute>
                } />
                <Route path="/update_form/:id" element={
                    <PrivateRoute>
                        <UpdateForm/>
                    </PrivateRoute>
                } />
                <Route path="/dashboard" element={
                    <PrivateRoute>
                        <Dashboard/>
                    </PrivateRoute>
                } />
            </Routes>
        </Router>
    </Provider>


);

export default App