import React, { useState, useEffect } from 'react'
import { Navigate, Link } from 'react-router-dom'
import axios from 'axios'
import { Table } from 'react-bootstrap'
import ReactPaginate from 'react-paginate'
import { BiSearch } from "react-icons/bi";
import DayJS from 'react-dayjs'

const PER_PAGE = 10

function InputData() {

  const [getDatas, setGetDatas] = useState([])
  const [currentPage, setCurrentPage] = useState(0)
  const [searchDate, setSearchDate] = useState("")
  const [searchDate2, setSearchDate2] = useState("")

  const minDate = Date.parse(searchDate)
  const maxDate = Date.parse(searchDate2)

  console.log(searchDate)
  console.log(searchDate2)

  useEffect(() => {
    axios.get(`${process.env.REACT_APP_API_URL}/input/input_data_ret`)
      .then((response) => {
        setGetDatas(response.data)
      })
      .catch(error => console.error(`Error: ${error}`))
  }, [])

  function handlePageClick({ selected: selectedPage }) {
    setCurrentPage(selectedPage)
  }

  const offset = currentPage * PER_PAGE

  const currentPageData = getDatas
    .slice(offset, offset + PER_PAGE)

  const pageCount = Math.ceil(getDatas.length / PER_PAGE)

  const filteredData = currentPageData.filter(data => Date.parse(data.date) >= minDate && Date.parse(data.date) <= maxDate).slice(offset, offset + PER_PAGE)
  const pageCountFilter = Math.ceil(filteredData.length / PER_PAGE)

  return (
    <div className='container mt-5'>
      <Link to="/input_form" className='btn btn-primary'>New data</Link>
      <h6 className="text-left mb-3 mt-3">Filter By:</h6>
      <div className='form-group d-flex'>
        <div>
          <label htmlFor="date-filter">From</label>
          <input type="date" placeholder='Date' className='form-control' name="start-date" value={searchDate} onChange={event => { setSearchDate(event.target.value) }} />
        </div>
        <div>
          <label htmlFor="date-filter">To</label>
          <input type="date" placeholder='Date' className='form-control' name="end-date" value={searchDate2} onChange={event => { setSearchDate2(event.target.value) }} />
        </div>
      </div>
      <div className='container mt-5'>
        <Table pagination={true}>
          <thead>
            <tr>
              <th>id</th>
              <th>Estate</th>
              <th>Division</th>
              <th>Weight(MT)</th>
              <th>Date</th>
            </tr>
          </thead>
          <tbody>
            {
              searchDate === "" & searchDate2 === "" ? (
                currentPageData.map((getData) => {
                  return (
                    <tr>
                      <td>{getData.id}</td>
                      <td>{getData.estate}</td>
                      <td>{getData.division}</td>
                      <td>{getData.weight}</td>
                      <td>{getData.date}</td>
                      <td><Link className='btn btn-primary' to={`/update_form/${getData.id}`}>Edit</Link></td>
                    </tr>
                  )
                })
              ) : (
                filteredData.map((getData) => {
                  return (
                    <tr>
                      <td>{getData.id}</td>
                      <td>{getData.estate}</td>
                      <td>{getData.division}</td>
                      <td>{getData.weight}</td>
                      <td>{getData.date}</td>
                      <td><Link className='btn btn-primary' to={`/update_form/${getData.id}`}>Edit</Link></td>
                    </tr>
                  )

                })
              )
            }
          </tbody>
        </Table>
      </div>
      {
        searchDate === "" && searchDate2 === "" ? (
          <ReactPaginate
            breakLabel="..."
            nextLabel="next >"
            onPageChange={handlePageClick}
            pageRangeDisplayed={5}
            pageCount={pageCount}
            previousLabel="< previous"
            renderOnZeroPageCount={null}
            containerClassName={"pagination"}
            pageClassName={'page-item'}
            pageLinkClassName={'page-link'}
            previousClassName={'page-item'}
            previousLinkClassName={'page-link'}
            nextClassName={'page-item'}
            nextLinkClassName={'page-link'}
            breakClassName={'page-item'}
            breakLinkClassName={'page-link'}
            activeClassName={'active'}
          />
        ) : (
          <ReactPaginate
            breakLabel="..."
            nextLabel="next >"
            onPageChange={handlePageClick}
            pageRangeDisplayed={5}
            pageCount={pageCountFilter}
            previousLabel="< previous"
            renderOnZeroPageCount={null}
            containerClassName={"pagination"}
            pageClassName={'page-item'}
            pageLinkClassName={'page-link'}
            previousClassName={'page-item'}
            previousLinkClassName={'page-link'}
            nextClassName={'page-item'}
            nextLinkClassName={'page-link'}
            breakClassName={'page-item'}
            breakLinkClassName={'page-link'}
            activeClassName={'active'}
          />
        )
      }
      {/* <ReactPaginate
        breakLabel="..."
        nextLabel="next >"
        onPageChange={handlePageClick}
        pageRangeDisplayed={5}
        pageCount={pageCount}
        previousLabel="< previous"
        renderOnZeroPageCount={null}
        containerClassName={"pagination"}
        pageClassName={'page-item'}
        pageLinkClassName={'page-link'}
        previousClassName={'page-item'}
        previousLinkClassName={'page-link'}
        nextClassName={'page-item'}
        nextLinkClassName={'page-link'}
        breakClassName={'page-item'}
        breakLinkClassName={'page-link'}
        activeClassName={'active'}
      /> */}
    </div >

  )
}

export default InputData