import React, { Component } from 'react'
import {Link} from 'react-router-dom'

export default class Home extends Component {
  render() {
    return (
      <div className='container'> 
        <div className='mt-5 p-5 bg-light'>  
            <h1 className='display-4'>Welcome to POMAS</h1>
            <p className='lead'>Lorem ipsum dolor sit amet consectetur adipisicing elit. Ipsum quam, qui, minus deserunt modi reprehenderit sapiente incidunt assumenda ex veniam blanditiis. Enim optio adipisci porro odit voluptate delectus architecto alias!</p>
            <hr className='my-4'/>
            <p>Click the button below to login</p>
            <Link className='btn btn-primary btn-lg' to="/login">Login</Link>
        </div>
      </div>
    )
  }
}
