import Cookies from 'js-cookie'
import axios from 'axios'
import {
    INPUT_DATA_UPDATE_SUCCESS,
    INPUT_DATA_UPDATE_FAIL,
    UPDATE_DATA_SUCCESS,
    UPDATE_DATA_FAIL
} from './types'
import { toast } from 'react-toastify'
import { useNavigate, useParams } from 'react-router-dom'


export const add_input_data = (estate, division, weight, date) => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'X-CSRFToken': Cookies.get('csrftoken')
        }
    }

    const body = JSON.stringify({
        'withCredentials': true,
        estate,
        division,
        weight,
        date
    })

    try {
        const res = await axios.post(`${process.env.REACT_APP_API_URL}/input/input_data`, body, config)
        
        if (res.data.error) {
            dispatch({
                type: INPUT_DATA_UPDATE_FAIL,
            })
            console.log(res.data.error)
            toast.error('Same date, estate and division have already been captured from previous entries', {
                position: "top-center",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                });
        }
        else if (res.data.estate && res.data.division) {
            dispatch({
                type: INPUT_DATA_UPDATE_SUCCESS,
                payload: res.data
            })
            toast.success("Data Successfully Captured", {
                position: "top-center",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            })
        } else{
            dispatch({
                type: INPUT_DATA_UPDATE_FAIL,
            })
        }
    } catch (err) {
        dispatch({
            type: INPUT_DATA_UPDATE_FAIL
        })
    }
}


export const update_data = (id, estate, division, weight, date) => async dispatch => {
    const config = {
        headers:{
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'X-CSRFToken' : Cookies.get('csrftoken')
        }
    }

    const body = JSON.stringify({
        'withCredentials' : true,
        estate,
        division,
        weight,
        date
    })


    try {
        const res = await axios.put(`${process.env.REACT_APP_API_URL}/input/update_data/${id}/`, body, config)

        if (res.data.estate && res.data.division && res.data.weight && res.data.date) {
            dispatch({
                type: UPDATE_DATA_SUCCESS,
                payload: res.data
            })
            toast.success("Data Successfully Updated", {
                position: "top-center",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            })
        } else {
            dispatch({
                type: UPDATE_DATA_FAIL
            })
        }
    } catch (err) {
        dispatch({
            type: UPDATE_DATA_FAIL
        })
    }
}